import React from 'react';
import '../../../Styles/QuizCards.css'; //css for the quiz cards

function QuizCards(props){ //props contains the quiz card data and quizStart function
    return (
        <div className={"card quiz-card m-2 col-11 "}>
            <img className="card-img" src={props.data.imgUrl} style={{width: "300px"}} alt="quiz1" />
            <div className="card-body d-flex flex-column justify-content-between">
                <h5 className="card-title">{props.data.title}</h5>
                <span 
                    className="time"
                    title="Total Time Given"
                >
                    {props.data.noQues*30/60} Minutes 
                    <div 
                        style={{position: "absolute",right:0,top: "-20px"}}
                    >
                        <i className="material-icons">timer</i>
                    </div>
                </span>
                <p className="card-text ">
                    <b>Difficulty</b>: {props.data.difficulty} <br/>
                    <b>No. of Questions</b>: {props.data.noQues}<br/>
                    
                    <span className="card-desc"><br />{props.data.description}<br/></span>
                </p>
                <button 
                    type="button" 
                    className="btn btn-css w-100"
                    onClick={()=>props.startQuiz(props.data.id)}
                >
                    Play Quiz
                </button>
            </div>
        </div>
    )
}

export default QuizCards;