import React,{Component} from 'react';

import Quiz from './MainComponents/Quiz'; //displays all the quiz cards
import Leaderboard from './MainComponents/Leaderboard'; //displays all the players and thier highscores

import '../Styles/Main.css'; //css for main content



class Main extends Component {
    
    constructor(props){
        super();
        this.state = {
            page: 0, //whether to show quizes or leaderboard
            currentPage: <Quiz quizData={props.quizData} quizStart={props.quizStart}/> //whether to show quizes or leaderboard
        }
    }

    

    //Changes the main content to display all Quiz cards 
    changeQuiz = () => {
        const {quizData, quizStart} = this.props;
        
        this.setState({
            page: 0, //page 0 is quiz cards page
            currentPage: <Quiz quizData={quizData} quizStart={quizStart}/> //displays all the quizcards
        })
    }

    //Changes the main content to display the leaderboard 
    changeLeaderboard = () => {
        if(this.state.page!==1){
            this.setState({
                page: 1, //page 1 is leaderboard page
                currentPage: <Leaderboard /> //displays leaderboard
            });
        }
    }
    
    render(){
        return (
            <div id="homepage">
                <div className="switch-btn-container">
                    <div className="btn-group-lg" role="group">
                        <div id="quiz-switch" type="button" className={"btn switch border " +( this.state.page===0 && "btn-selected")} onClick={this.changeQuiz}>Quiz</div>
                        <div id="leaderboard-switch" className={"btn switch border " +( this.state.page===1 && "btn-selected")} onClick={this.changeLeaderboard}>Leaderboard</div>
                    </div>
                </div>
                <div id="main-page" className="container" style={{position: "relative"}}>
                    <h1 id="mainpage-heading">{this.state.page===0 ? "Quiz" : "Leaderboard"} 
                    </h1>
                    
                    {this.state.currentPage}
                </div>
            </div>
        )
    }
}

export default Main;