import React,{Component} from 'react';
import '../Styles/QuizApp.css';
import QuizEnd from './QuizEnd';
import QuizTransition from './QuizTransition';
import Radium,{StyleRoot} from 'radium';
import {fadeInLeft,slideInDown, fadeInRight, slideInLeft, slideInRight, fadeIn} from 'react-animations';
import $ from 'jquery'

//imports for timer audios
import timer1 from '../static/shared-resources/timer1.mp3';
import timer2 from '../static/shared-resources/timer2.mp3';

//notify animation (style object)
const notiAnimation = {
    animation: "0.6s ease-in-out",
    zIndex: -1,
    animationName: Radium.keyframes(slideInDown,"slideInDown")
}

//que num animation (style object)
const queNumAnimation = {
    animation: "0.6s ease-in-out",
    animationName: Radium.keyframes(fadeInLeft,"fadeInLeft")
}

//question animation (style object)
const queAnimation = {
    animation: "0.6s ease-in-out",
    animationName: Radium.keyframes(slideInDown,"slideInDown")
}

//que img animation (style object)
const queImgAnimation = {
    animation: "0.6s ease-in-out",
    animationName: Radium.keyframes(fadeIn,"slideInDown")
}

//timer animation (style object)
const timerAnimation = {
    animation: "0.6s ease-in-out",
    zIndex: -1,
    animationName: Radium.keyframes(fadeInRight,"fadeInRight")
}

//next and finish btn animation (style object)
const nFBtnAnimation = {
    animation: "0.6s ease-in-out",
    animationName: Radium.keyframes(fadeInRight,"fadeInRight")
}

//options on Left animation (style object)
const optionLeftAnimation = {
    animation: "0.6s ease-in-out",
    animationName: Radium.keyframes(slideInLeft,"slideInLeft")
}

//options on right animation (style object)
const optionRightAnimation = {
    animation: "0.6s ease-in-out",
    animationName: Radium.keyframes(slideInRight,"slideInRight")
}

//menu items style object
const menuItemsStyle = {
    position: "absolute",
    top: "48px",
    right: "20px",
    width: "40px",
    height: "100px",
    background: "rgb(245, 245, 245)",
    border: "1px solid lightgrey",
    borderRadius: "6px"
}

//volume and exit icon style object
const volumeAndExitStyle = {
    position: "fixed",
    right: "26px",
    zIndex: 6,
    cursor: "pointer",
    color: "white"
}

//menu btn style object
const menuBtnStyle = {
    position: "fixed",
    top: "10px",
    right: "0",
    zIndex: 6,
    cursor: "pointer",
    color: "white",
}


class QuizApp extends Component {
    constructor(props) {  //props contains questions, username and changePage function
        super();
        this.state = {
            title:props.title, //curr quiz title
            ques: props.questions, //all the questions of current quiz
            opt1: -1, //if option 1 selected
            opt2: -1, //if option 2 selected
            opt3: -1, //if option 3 selected
            opt4: -1, //if option 4 selected
            answered: false, //whether the question is answered
            count: props.getCount(), //question number
            score: props.getScore(), //final score
            time: 30, //per question timer
            notification: -1, //show correct notification
            intervalID: null, //interval id of timer
            showNext: false, //whether to disable next button
            showFinish: false, //finish button appear or not
            timerInterval: null, //timer inerval id
            timer: null, //timer sound
        }
    }


    //when any option is selected
    handleSelect=(opt)=>{

        //checking the selected option
        this.setState({
            answered: true, //question is answered (option selected)
            showNext: true //show next button
        })

        //clears timer intervals and hide timer
        clearInterval(this.state.intervalID);
        clearInterval(this.state.timerInterval);
        document.querySelector(".timer").style.display = 'none';
        this.state.timer.pause();

        //selected option is correct or not
        if(this.state.ques[this.state.count].correctOpt === opt){
            this.props.updatefromChild(this.state.count,this.state.score+10)
            this.setState(()=>{
                return{ score: this.props.getScore(), //increase score on correct selected option
                    notification: 1, //showing correct notification on correct selected option
                    [this.state.ques[this.state.count].correctOpt]: 1 //making the selected and correct option green
                }
            })
        }else{   

            //for ticking the notification and selected option to wrong(red) and correct option to green
            this.setState({
                notification: 0, //setting notification to wrong
                [opt]: 0, //setting selected option to wrong(red)
                [this.state.ques[this.state.count].correctOpt]: 1 //showing correct option by green color
            })
        }

        //for finish button to appear after the last question submit
        if(this.state.count===this.state.ques.length-1){
            this.setState({
                showNext:false,  //next should not be shown on the last question
                showFinish: true //finish button should be shown on last question
            })
        }
        


    }

    //changes page to quiz transition on next
    toQuizTransition= ()=>{
        this.props.changePage(<QuizTransition transitionSticker={this.props.transitionSticker} username={this.props.username} currentScore={this.state.score} changePage={this.props.changePage} toQuizApp={this.props.toQuizApp} />)
    }

    // when next button is clicked
    handleNext = () => {
        if(this.state.count<this.state.ques.length-1) { //Next only if question length is not exceeded
            //resetting the options and buttons + Increase counter
            this.props.updatefromChild(this.state.count+1,this.state.score)
            this.setState(()=>({
                    answered: false, //option is selected or not
                    opt1: -1, //setting all options to default
                    opt2: -1,
                    opt3: -1,
                    opt4: -1,
                    count: this.props.getCount(), //INCREASE COUNT
                    showNext: false, //next button hidden again
                    notification: -1 //setting the notification to default (no  notification)
            }))
        }
        
        this.toQuizTransition(); //go to quiz transition page
    }



    //handles the timer of each question
    handleTimer = () => {
        var newTime = this.state.time-1; //decreasing count by 1
        if(newTime>=1){  //if decreased time is not less than 0
            this.setState({
                time: newTime //decreased time is set in the state
            })
        }else{
            clearInterval(this.state.intervalID); //clears the timer after reaching 0
            this.state.timer.pause();
            this.setState({
                time: newTime, //decreased time is set to 0
                answered: true, //sets the answered to true so that options are disabled
                showNext: true, //next button is visible after time is up
                notification: 2, //shows time up notification
                [this.state.ques[this.state.count].correctOpt]: 1 //shows the correct answer
            })

            //if on the last question
            if(this.state.count===this.state.ques.length-1){ 
                this.setState({
                    showNext: false, //do not show next button
                    showFinish: true //show finish button
                })
            }
        }

        //when timer is at 11s
        if(newTime===11){ 
            //stop timer1 and play timer2
            this.props.timerToggle(false,null);
            let timer1Audio=this.state.timer;
            let timer2Audio = new Audio(timer2);
            this.setState({timer: timer2Audio})
            
            setTimeout(()=>{
                if(timer1Audio.volume !== 0)
               {timer1Audio.volume = 0;}
                timer2Audio.play();
            },500)
        }
        
    }
   
    //change page to QuizEnd when finish btn is pressed
    handleFinish=()=>{
        this.props.changePage(<QuizEnd audioStop={this.props.audioStop} score={this.state.score} username={this.props.username} title={this.state.title}  changePage={this.props.changePage}  toHomePage={this.props.toHomePage} toLoading={this.props.toLoading} updatefromChild={this.props.updatefromChild}/>);
    }

    componentDidMount(){

        //set style of icons and change color to black
        $(".menu-items").css(menuItemsStyle);
        $(".toggle-volume").css(volumeAndExitStyle)
        $(".exit").css(volumeAndExitStyle)
        $(".menu-btn").css(menuBtnStyle)
        $(".toggle-volume").css({color:"black"})
        $(".menu-btn").css({color: "black"})
        $(".exit").css({color: "black"})
        $(".menu-btn").show();
        $(".menu-items").hide();

        //timer's circle animation 
        const FULL_DASH_ARRAY = 283;
        const WARNING_THRESHOLD = 10;
        const ALERT_THRESHOLD = 5;

        const COLOR_CODES = {
            info: {
                color: "green"
            },
            warning: {
                color: "orange",
                threshold: WARNING_THRESHOLD
            },
            alert: {
                color: "red",
                threshold: ALERT_THRESHOLD
            }
        };

        const TIME_LIMIT = this.state.time;
        let timePassed = 0;
        let timeLeft = TIME_LIMIT;
        let remainingPathColor = COLOR_CODES.info.color;

        document.querySelector(".timer").innerHTML = `
            <div class="base-timer">
                <svg class="base-timer__svg" viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
                    <g class="base-timer__circle">
                    <circle class="base-timer__path-elapsed" cx="50" cy="50" r="45"></circle>
                    <path
                        id="base-timer-path-remaining"
                        stroke-dasharray="283"
                        class="base-timer__path-remaining ${remainingPathColor}"
                        d="
                        M 50, 50
                        m -45, 0
                        a 45,45 0 1,0 90,0
                        a 45,45 0 1,0 -90,0
                        "
                    ></path>
                    </g>
                </svg>
                <span id="base-timer-label" class="base-timer__label">
                    ${formatTime(timeLeft)}
                </span>
            </div>
        `;


        const onTimesUp = () => {
            clearInterval(this.state.timerInterval);
            document.querySelector(".timer").style.display = "none";
        }

        const startTimer = () => {
            let timerInterval = setInterval(() => {
                timePassed = timePassed += 1;
                timeLeft = TIME_LIMIT - timePassed;
                document.getElementById("base-timer-label").innerHTML = formatTime(timeLeft);
                setCircleDasharray();
                setRemainingPathColor(timeLeft);

                if (timeLeft === 0) {
                    onTimesUp();
                }
            }, 1000);

            this.setState({timerInterval: timerInterval})
        }

        startTimer();

        function formatTime(time) {
            const minutes = Math.floor(time / 60);
            let seconds = time % 60;

            if (seconds < 10) {
                seconds = `0${seconds}`;
            }
            return `${minutes}:${seconds}`;
        }

        function setRemainingPathColor(timeLeft) {
            const { alert, warning, info } = COLOR_CODES;
            if (timeLeft <= alert.threshold) {
                document.getElementById("base-timer-path-remaining").classList.remove(warning.color);
                document.getElementById("base-timer-path-remaining").classList.add(alert.color);
            } else if (timeLeft <= warning.threshold) {
                document.getElementById("base-timer-path-remaining").classList.remove(info.color);
                document.getElementById("base-timer-path-remaining").classList.add(warning.color);
            }
        }

        function calculateTimeFraction() {
            const rawTimeFraction = timeLeft / TIME_LIMIT;
            return rawTimeFraction - (1 / TIME_LIMIT) * (1 - rawTimeFraction);
        }

        function setCircleDasharray() {
            const circleDasharray = `${(
                calculateTimeFraction() * FULL_DASH_ARRAY
            ).toFixed(0)} 283`;
            document.getElementById("base-timer-path-remaining").setAttribute("stroke-dasharray", circleDasharray);
        }

        //start the timer interval
        var intervalID = setInterval(this.handleTimer,1000); 
        this.setState({intervalID: intervalID}); //the timer interval id will be saved in the state
        
        //play timer1 audio when timer starts
        let timer1Audio = new Audio(timer1);
        this.setState({timer: timer1Audio});
        
        this.props.timerToggle(true,timer1Audio);
       
        setTimeout(()=>{
            timer1Audio.play(); //play timer1 audio
            if($(".toggle-volume i").html() === "volume_off"){ //if volume is off
                timer1Audio.volume=0; //mute the timer1
            }
        },600);
    }
   
    componentWillUnmount(){

        //pause timer audio and clear timer intervals
        this.state.timer.pause();
        clearInterval(this.state.timerInterval);
        clearInterval(this.state.intervalID); //when the component unmounts previous timer will be cleared
    }

    render(){
      
        const ques = this.state.ques; //all the questions of current quiz
        const count = this.state.count; //question number

        let noti; //notification text to be shown if wrong, right or time up
        let notiStyle; //notification style to be set if wrong, right or time up

        //default -- notification not displayed
        if(this.state.notification===-1){
            noti = "";
            notiStyle="default"
        }
        //if option is correct
        else if(this.state.notification===1){
            noti = "Correct!"
            notiStyle="correct"
        }
        //if option is wrong
        else if(this.state.notification===0){  
            noti = "Wrong!"
            notiStyle="wrong"
        }
        //if time is up before selection
        else if(this.state.notification===2){
            noti = "Time Up!"
            notiStyle="timeup"
        }

        return (
            <StyleRoot>
            <div className="quiz-app-container">
                <div className="question-container" style={queAnimation}>
                    <div className="que" >{ques[count].que}</div>
                </div>

                <div className="middle-container">
                    <div className="que-number" style={[queNumAnimation,{position:"absolute",left: "0"}]}>{ques[count].queNo}</div>
                    <div className="timer-container" style={timerAnimation}>
                        <div className="timer"></div>
                    </div>
                    
                    <div className="que-img" style={[queImgAnimation,{marginTop: (10+"px" ),backgroundImage: "URL("+ques[count].queImg+")"}]}>  
                        
                    </div>

                    <div className={"notification " + notiStyle} style={notiAnimation}>
                        <div>{noti}</div>
                    </div>

                    
                    
                    <div>
                    
                        <button 
                            style={nFBtnAnimation} 
                            className={"next-btn " + (!this.state.showNext && "d-none")} 
                            onClick={this.handleNext}
                        >
                            Next
                        </button>
                        <button 
                            style={nFBtnAnimation} 
                            className={"finish-btn " + (!this.state.showFinish && "d-none")} 
                            onClick={this.handleFinish}
                        >
                            Finish
                        </button>
                        
                    </div>
                </div>
                <div className="option-container">

                    <button
                        className={"option " +(this.state.answered ? "" : "opt1")+" " + (this.state.opt1===1 ? "correctAnswer": this.state.opt1===0 ? "wrongAnswer": "" )} 
                        disabled={this.state.answered}
                        data={this.state.answered?"true":"false"}
                        onClick={()=>this.handleSelect("opt1")}
                        style={[optionLeftAnimation,{height: (ques[count].opt3===""?"140px":""),fontSize:(ques[count].opt3===""? "6vmin": "")}]}
                    >
                        <span style={{margin: "0 15px"}}>{ques[count].opt1}</span>
                        <i className='material-icons' style={{position: "absolute", right: "10px"}}></i>
                    </button>

                    <button 
                        className={"option " +(this.state.answered ? "" : "opt2")+" " + (this.state.opt2===1 ? "correctAnswer": this.state.opt2===0 ? "wrongAnswer": "" ) } 
                        disabled={this.state.answered}
                        data={this.state.answered?"true":"false"}
                        onClick={()=>this.handleSelect("opt2")}
                        style={[optionRightAnimation,{fontSize:(ques[count].opt3===""? "6vmin": "")}]}
                    >
                        <span style={{margin: "0 15px"}}>{ques[count].opt2}</span>
                        <i className='material-icons' style={{position: "absolute", right: "10px"}}></i>
                    </button>

                    <button 
                        className={"option " +(this.state.answered ? "" : "opt3")+" " + (this.state.opt3===1 ? "correctAnswer": this.state.opt3===0 ? "wrongAnswer": "" ) } 
                        disabled={this.state.answered}
                        data={this.state.answered?"true":"false"}
                        onClick={()=>this.handleSelect("opt3")}
                        style={[optionLeftAnimation,{display: (ques[count].opt3===""?"none":"")}]}
                    >
                        <span style={{margin: "0 15px"}}>{ques[count].opt3}</span>
                        <i className='material-icons' style={{position: "absolute", right: "10px"}}></i>
                    </button>

                    <button 
                        className={"option " +(this.state.answered ? "" : "opt4")+" " + (this.state.opt4===1 ? "correctAnswer": this.state.opt4===0 ? "wrongAnswer": "" ) } 
                        disabled={this.state.answered}
                        data={this.state.answered?"true":"false"}
                        onClick={()=>this.handleSelect("opt4")}
                        style={[optionRightAnimation,{display: (ques[count].opt3===""?"none":"")}]}
                    >
                        <span style={{margin: "0 15px"}}>{ques[count].opt4}</span>
                        <i className='material-icons' style={{position: "absolute", right: "10px"}}></i>
                    </button>

                </div>
            </div>
            </StyleRoot>
        )
    }
}

export default QuizApp;