import React from "react";
import $ from "jquery";
import "../Styles/Greet.css";

import introTheme from  '../static/shared-resources/intro.mp3';

export default class Greet extends React.Component {

    constructor(){
      super();
      this.state = {
        intro: null //stores intro theme object
      }
    }

    //greet logo animation for greet page
    animateGreet=()=>{
        $("#GreetHead1").animate({fontSize:"30vw"},2000);
        setTimeout(()=>{$("#GreetDiv1").animate({left: "12vw"},1000);},2000);
        setTimeout(()=>{$("#GreetDiv2").css({display:"inherit"});},3000);
        setTimeout(()=>{$("#GreetOuter").animate({opacity:"0"},1500);},3500);
        setTimeout(()=>{$("#GreetOuter").css({display:"none"})},5000);
    }

    componentDidMount()
    {
      //plays and sets the introtheme to state
      let intro = new Audio(introTheme); 
      this.setState({intro: intro}) 
      setTimeout(()=>{
          intro.play();
      },10)

      //hide volume icon
      $(".toggle-volume").hide()
      //starts greet logo animation
      this.animateGreet();
    }

    componentWillUnmount(){
      this.state.intro.pause() //pause the intro when unmounts
    }

    render()
    {
      return (
        <div id="GreetOuter">
          <div id="GreetDiv1">
            <h1 id="GreetHead1"> Q </h1>
          </div>
          <div id="GreetDiv2">
            <h1 id="GreetHead2">UIZ MANIA</h1>
          </div>
        </div>
      );
    }
  
  }