import React from 'react';
import '../Styles/QuizTransition.css';
import Radium, {StyleRoot} from 'radium';
import { slideInDown, tada, zoomIn} from 'react-animations';
import $ from 'jquery'

//payername animation (Style object)
const playernameAnimation = {
    animation: "0.6s ease-in-out",
    animationName: Radium.keyframes(slideInDown,"slideInDown")
}

//curr score animation (Style object)
const currentScoreAnimation = {
    animation: "0.5s ease-in-out",
    animationName: Radium.keyframes(zoomIn,"zoomIn")
}

//sticker animation (Style object)
const stickerAnimation = {
    animation: "1.5s ease-in-out",
    animationName: Radium.keyframes(tada,"bounceIn")
}

class QuizTransition extends React.Component{

    constructor(props){
        super();
        this.state={
            intervalID:null, //interval id for loading progress bar
            loadPercentage:0, //percentage of loading progress bar
            currentScore:props.currentScore //current score
        }
    }
    handleLoadPercentage=()=>{
          var newLoadPercentage=this.state.loadPercentage+1; //increase loading progress bar percentage
          
          //play loading animation and go back to quiz app after completing
          if(newLoadPercentage<=200) 
          {
              this.setState({
                loadPercentage:newLoadPercentage
              })
          }
          else{
              clearInterval(this.state.intervalID)
              this.props.toQuizApp();
        
          }
    }
    componentDidMount(){
        //css for menu btn and items
        $(".menu-btn").show().css({color: "white"})
        $(".toggle-volume").show().css({color: "black"})
        $(".exit").show().css({color: "black"})

        //sets the interval for loading progress bar
        var intervalID = setInterval(this.handleLoadPercentage,10);
        this.setState({intervalID: intervalID});
    }

    componentWillUnmount(){
        clearInterval(this.state.intervalID); //clear loading interval when it unmounts
    }

    render(){
        return (
            <StyleRoot>
                <div className="quiz-transition-container">
                    <div className="playername-container" >
                        <div className="playername" style={playernameAnimation}>{this.props.username.toUpperCase()}</div>
                    </div>
                    <div className="transition-img" style={[stickerAnimation,{width: "200px",height: "200px",backgroundPosition: "center",backgroundRepeat:"no-repeat",backgroundSize: "contain",backgroundImage: "URL("+this.props.transitionSticker+")"}]}>
                    </div>
                    <div className="curr-score-container" style={currentScoreAnimation}>
                        <div className="curr-score-title">
                            Current Score
                        </div>
                        <div className="curr-score">
                            {this.state.currentScore}
                        </div>
                    </div>
                    <div className="progressbar-container">
                        <div className="progress" style={{borderRadius: "0px", background: "transparent"}}>
                            <div 
                                className="progress-bar progress-bar-animated" 
                                role="progressbar" 
                                aria-valuenow={75} 
                                aria-valuemin={0}
                                aria-valuemax={100} 
                                style={{width: this.state.loadPercentage+"%"}}
                            ></div>
                        </div>
                    </div>
                </div>
            </StyleRoot>
        )
    }
}

export default QuizTransition;
