import React from 'react';
import $ from 'jquery';

import QuizCards from "./QuizComponents/QuizCards";

//animation for quiz cards
const animateCards = ()=>{
    let quizContLen = $("#quiz-container").height();
    let quizCardLen = $(".quiz-card").height();
    $(".quiz-card").animate({top: (-quizContLen-quizCardLen)+"px"},0).animate({top:"0"},1000);


}

class Quiz extends React.Component{
    constructor(){
        super();
        this.state = {
            search:"" //search input value
        }
    }

    componentDidMount(){
        //start quiz card animation
        animateCards();
    }

    //handles the searching for quizes
    handleSearch = (event) => {
        const {value} = event.target;
        this.setState({search: value})
    }

    render(){
        const quizStart = this.props.quizStart; //stores startQuiz function
        //filter quizData acc. to search input
        const filteredData = this.props.quizData.filter((data) => {
            return data.title.toLowerCase().includes(this.state.search.toLowerCase())
        })

        //quiz cards to be displayed
        const quizCardComponents = filteredData.map((quizcard) => { //returns all the card components with thier data
            return (
                <QuizCards key={quizcard.id} data={quizcard} startQuiz={quizStart}/> 
            )
        })
        
        return (
            <div>
                <div id="quiz-container" className="m-4 d-flex flex-column justify-content-center">
                    <div className="search-container input-group">
                        <label className="input-group-prepend"><i className="material-icons search-btn input-group-text" >search</i><input className="form-control" type='text' placeholder="Search" value={this.state.search} onChange={this.handleSearch}/></label>
                    </div>
                    {quizCardComponents.length!==0 ? quizCardComponents : (<h4 style={{marginTop: "30px",textAlign: "center"}}>No Results Found!</h4>)}
                </div>
            </div>
        )
    }
}

export default Quiz;