import React from 'react';
import '../Styles/Info.css';
import $ from 'jquery';
import ContactCard from './ContactCard';

//avatar imports
import avatar1 from '../static/shared-resources/vishalavatar.png'
import avatar2 from '../static/shared-resources/abhishekavatar.png'
import avatar3 from '../static/shared-resources/yogeshavatar.png'
import avatar4 from '../static/shared-resources/yoganshavatar.png'

class Info extends React.Component{
   
    componentDidMount(){
        if(window.sessionStorage.getItem("Initial")===null) //if first time in the current session
        {
            //plays info icon animation by adding 'info-anim' class
            $(".info-icon~div").addClass("info-anim");
            setTimeout(()=>{ $('.contact-us').animate({width:"150px"},500)},500)
            setTimeout(()=>{
                //stops info icon animation by removing 'info-anim' class
                $(".info-icon~div").removeClass("info-anim");
                $(".contact-us").animate({width:"0px"},500);
            },2300);
       }   
    }

    //static variable for toggling contact us animation
    static contactUs=true;

    //toggle the contact us details
    static handleInfo=()=>{
        if(Info.contactUs) //if contactus is true
        {
            //play the contact us animation
            $('.contact-us').animate({width:"150px"},500);
            setTimeout(()=>{$(".contact-card-outer").toggle(300)},200);
            Info.contactUs=false;
        }else{
            //undo the contact us animation
            $(".contact-us").animate({width:"0px"},500);
            setTimeout(()=>{$(".contact-card-outer").toggle(300)},150);
            Info.contactUs=true;
        }
    }
    
    render()
    {
        return(
            <div>
                <div className="contact-us" style={{position:"absolute", boxSizing: "border-box" , width:"0px", height:"20px",padding:"0px 0px 25px 10px",backgroundColor:"white", border:"2px solid lightgrey", right:"20px" , top:"13.7px" , zIndex:"3", borderRadius:"100px", overflow:"hidden"}}><span>Contact Us</span></div>
                <div className="info-icon-container unselectable"  onClick={Info.handleInfo} style={{position:"absolute" , right:"10px" , top:"10px" , zIndex:"3"}}>
                    <i className="material-icons info-icon" style={{color:"#1368CE", fontSize:"2.2rem"  , borderRadius:"50%",backgroundColor:"whitesmoke"}}>info</i>
                    <div style={{cursor: "pointer",position: "absolute",right:"4px",top: "4px",width: "28px",height: "28px",borderRadius: "50%"}}></div>
                </div>
                <div className="contact-card-outer"style={{position:"absolute" ,right:"30px" , top:"50px" , zIndex:"3",display:"none"}}>
                   <ContactCard  name="Vishal Aneja" avatar={avatar1} github="https://github.com/Vishal-1125" mail="mailto: vishal1125@chitkara.edu.in" linkedin="https://www.linkedin.com/in/vishal-aneja-1b09781b5/"/>
                   <ContactCard  name="Abhishek Gupta" avatar={avatar2} github="https://github.com/Abhishek1132" mail="mailto: abhishek1132@chitkara.edu.in" linkedin="https://www.linkedin.com/in/abhishek-gupta-9096361ba/"/>
                   <ContactCard  name="Yogesh Singla" avatar={avatar3} github="https://github.com/yogesh-ai" mail="mailto: yogesh1128@chitkara.edu.in" linkedin="https://www.linkedin.com/in/yogesh-singla-756327200"/>
                   <ContactCard  name="Yogansh Mahajan" avatar={avatar4} github="https://github.com/yogansh2409" mail="mailto: yogansh1127@chitkara.edu.in" linkedin="https://www.linkedin.com/in/yogansh-mahajan-b512b5212"/>
                </div>
            </div>
        )
    }
}
export default Info;