import React from 'react';
import '../Styles/ContactCard.css';

//imports social icons
import githubIcon from '../static/shared-resources/githubicon.svg';
import linkedinIcon from '../static/shared-resources/linkedinicon.svg';
import gmailIcon from '../static/shared-resources/gmailicon.png';

export default function ContactCard(props){
    return(
            <div className="contact-card-inner">
                <div className="contact-card-front">
                    <div style={{borderRadius: "50%",backgroundImage:"URL("+props.avatar+")", backgroundSize:"cover" , width:"1.8rem", height:"1.8rem", backgroundPosition:"center"}}></div>
                    <span className="unselectable">{props.name}</span>
                </div>
                <div className="contact-card-back" >
                    <a href={props.linkedin} target="_blank" rel="noopener noreferrer"><div style={{backgroundImage:"URL("+linkedinIcon+")", backgroundSize:"cover" , width:"1.9rem", height:"1.9rem", backgroundPosition:"center"}}></div></a>
                    <a href={props.mail} target="_blank" rel="noopener noreferrer"><div style={{backgroundImage:"URL("+gmailIcon+")", backgroundSize:"cover" , width:"1.9rem", height:"1.9rem", backgroundPosition:"center"}}></div> </a>
                    <a href={props.github} target="_blank" rel="noopener noreferrer"><div style={{backgroundImage:"URL("+githubIcon+")", backgroundSize:"cover" , width:"1.9rem", height:"1.9rem", backgroundPosition:"center"}}> </div></a>
                </div>
            </div>
    );
}