import React from 'react';
import Homepage from './Components/Homepage';
import QuizStart from './Components/QuizStart';
import Greet from './Components/Greet';
import quizCardsData from './QuizData/quizCardsData';
import $ from 'jquery'

class App extends React.Component {
  constructor() {
    super();

    this.state = {
      quizCardsData: quizCardsData,  //all quiz cards data
      displayPage: "" ,//which page or page component to display
      count:0, //question num
      score:0, //curr score
      themeSong: null, //background theme song played during quiz game
      video:false, //is Loading video playing
      timer:false, //if timer is running
      timerSound:null //timer sound
    }
    this.startQuiz = this.startQuiz.bind(this)
  }

  //for updating the count and score
  updatefromChild=(newCount,newScore)=>{
      this.setState({
        count:newCount, 
        score:newScore
      })
  }

  //returns the current count (ques num)
  getCount=()=>{
    return this.state.count
  }

  //returns the current score
  getScore=()=>{
    return this.state.score
  }

  //starts the quiz
  startQuiz(quizId){
    //change Page to QuizStart
    this.changePage(<QuizStart id={quizId} transitionSticker={this.state.quizCardsData[quizId].transitionSticker} gameAudio={this.gameAudio} themeSong={this.state.quizCardsData[quizId].themeSong} coverSong={this.state.quizCardsData[quizId].coverSong} audioStop={this.audioStop} videoToggle={this.videoToggle}  timerToggle={this.timerToggle} poster={this.state.quizCardsData[quizId].poster} questions={this.state.quizCardsData[quizId].quizQuestions} changePage={this.changePage} updatefromChild={this.updatefromChild} getCount={this.getCount}  getScore={this.getScore}  title={this.state.quizCardsData[quizId].title} defaultLoading={this.state.quizCardsData[quizId].defaultLoading} toHomePage={this.toHomePage}/>)
  }

  //function for changing the pages
  changePage = (page) => {
    this.setState({
      displayPage: page //sets the passed component to display
    })
  }

  //for going back to homepage
  toHomePage=()=>{
    this.updatefromChild(0,0); //resetting the que num count and score
    this.audioStop(); //stop the game audio
    $(".menu-btn").hide(); //hide menu btn 
    $('.toggle-volume').hide() //hide volume btn
    $('.exit').hide() //hide exit btn

    //change the page to Homepage
    this.changePage(<Homepage quizData={this.state.quizCardsData} quizStart={this.startQuiz} changePage={this.changePage}/>)
  }

  //for handling the game audio
  gameAudio =(sound)=>{ //sound file is passed
    let themeSong = new Audio(sound)  //audio object is created for the sound
    themeSong.loop = true; //audio loop true
    themeSong.volume = 0.3 //reduce volume a little 
    
    //set the themeSong playing currently
    this.setState({themeSong: themeSong})
    
    //play the song only if the audio is not muted
    if($(".toggle-volume i").html()==="volume_up"){
      themeSong.play() 
    }
  }

  //checks if the video is playing and sets the state acc.
  videoToggle=(check)=> {
    this.setState({video:check});
  }

  //checks if the timer audio is playing and sets the state of timer and timerSound acc.
  timerToggle=(check,timerSound)=>{
    this.setState({timer:check, timerSound:timerSound})
  }

  //used to toggle game audio
  audioToggle = () => {
    
    //if loading video is playing
    if(this.state.video)
    {
      if($(".loading-container video").prop("muted")) //if loading video is muted
      {
        $(".toggle-volume i").html("volume_up"); //toggle volume icon to on
        $(".loading-container video").prop("muted",false) //unmutes the loading video
      }else{ //if loading video is unmuted
        $(".toggle-volume i").html("volume_off"); //toggle volume icon to off
        $(".loading-container video").prop("muted",true) //mutes the loading video
      }
    }else{ //if loading video is not playing
      if(this.state.timer) //if timer is running
      {
        if(this.state.timerSound.volume === 0) //if timer audio is muted
        {
          //unmutes the timer audio and toggle volume icon to on
          let temp = this.state.timerSound; 
          temp.volume = 1;
          $(".toggle-volume i").html("volume_up");
        }else{ //if timer audio is unmuted
          //mutes the timer audio and toggle volume icon to off
          let temp = this.state.timerSound;
          temp.volume = 0;
          $(".toggle-volume i").html("volume_off");
        }
      }
      if(this.state.themeSong.paused){ //if themesong is paused
        //play themesong and toggle volume icon to on
        this.state.themeSong.play()
        $(".toggle-volume i").html("volume_up");
      }else{ //if themesong is not paused
        //pause themesong and toggle volume icon to off
        this.state.themeSong.pause()
        $(".toggle-volume i").html("volume_off");
      }
    }
  }

  //stops the themesong
  audioStop = () =>{
    if(this.state.themeSong!=null)
      this.state.themeSong.pause();
  }

  //toggle menu items when btn is clicked
  handleMenuItems = () => {
    $(".menu-items").toggle();
    $(".toggle-volume").css({top: "55px", right: "24px"})
    $(".exit").css({display: "inherit",top: "100px"})
  }


  //initializing the display page to display homepage component
  componentDidMount(){
    
    $(".toggle-volume").hide() //hides the volume icon
    if(window.sessionStorage.getItem("Initial")==null) //if first time in the current session
      {
        //change display to homepage after greet animation is finished
        setTimeout(()=>{
          this.toHomePage()
          window.sessionStorage.setItem("Initial","true");
        },4000);
      }
    else
    {
      //change page directly to Homepage
      this.toHomePage();
    }
  }

  render(){
    
    return (
      <div>
        {window.sessionStorage.getItem("Initial")==null && <Greet /> }
        <div className="menu-btn" onClick={this.handleMenuItems}><i className="material-icons" aria-selected={false}>more_vert</i></div>
          <div className="menu-items">
            <div className="toggle-volume" title="Game Audio" onClick={this.audioToggle}>
              <i className="material-icons">volume_up</i>
            </div>
            <div className="exit" title="Quit" style={{right: "26px", top: "95px", borderTop: "1px solid grey"}} onClick={this.toHomePage}><i className="material-icons" style = {{marginTop: "10px"}}>logout</i></div>
          </div>
          {this.state.displayPage}
      </div>
    )
  }
}

export default App;
