import React from 'react';
import Info from './Info'
import Header from './Header'; //the header component of homepage - (logo)
import Main from './Main'; //the main content component of homepage

function Homepage(props){ //props contains quizData and quizStart function
    document.querySelector("body").style.overflowY = "scroll"; //toggle scrollbarY to on
    return (
        <div>
            <Info />
            <Header />
            <Main quizData={props.quizData} quizStart={props.quizStart} />
        </div>
    )
}

export default Homepage;