import React from 'react';
import '../Styles/Header.css'; //css for header and logo

import Radium, {StyleRoot} from 'radium';
import {zoomInDown} from 'react-animations';

//logo animation (Style object)
const logoAnimation = {
    animation: "1.3s ease-in-out",
    animationName: Radium.keyframes(zoomInDown,"zoomInDown")
}

class Header extends React.Component{
    render(){
        return (
            <StyleRoot>
                <div className="logo-container">
                    <div className="unselectable" id="website-logo" style={logoAnimation}>
                        Quiz Mania
                    </div>
                </div>
            </StyleRoot>
        )
    }
}

export default Header;