import React from 'react';
import QuizApp from './QuizApp';
import '../Styles/QuizStart.css';
import Radium, {StyleRoot} from 'radium';
import {bounceInUp, bounceInDown, pulse} from 'react-animations';
import $ from 'jquery';

import loadingVisual from '../static/shared-resources/loadingvisual.mp4';

const nameAnimation = {
    animation: "1.3s ease-in-out",
    animationName: Radium.keyframes(bounceInDown,"bounceInDown")
}

const counterAnimation = {
    animation: "1s 3",
    animationName: Radium.keyframes(pulse,"flash")
}

//entry form div animation (style object)
const entryInputsAnimation = {
    animation: "1.85s ease-in-out",
    animationName: Radium.keyframes(bounceInUp,"bounceInUp")
}

// volume icon style object
const volumeToggleStyle = {
    display: "inherit",
    position: "fixed",
    top: "10px",
    right: "10px",
    zIndex: 6,
    cursor: "pointer",
    color: "white"
}

class Loading extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            count: (props.defaultLoading? 3 : 5), //loading counter
            intervalID: null //interval ID of the loading counter
        }
    }

    //changes to the current page to show QuizApp
    toQuizApp = ()=>{
        this.props.changePage(<QuizApp id={this.props.id} transitionSticker={this.props.transitionSticker}  gameAudio={this.props.gameAudio} themeSong={this.props.themeSong} audioStop={this.props.audioStop} timerToggle={this.props.timerToggle} questions={this.props.questions} username={this.props.username} changePage={this.props.changePage}  updatefromChild={this.props.updatefromChild} getCount={this.props.getCount}  getScore={this.props.getScore} toQuizApp={this.toQuizApp} title={this.props.title}  toHomePage={this.props.toHomePage} toLoading={this.props.toLoading}/>)
    }

    //handles the loading counter
    handleCounter = () => {
        var newCount = this.state.count-1;  //counter is decreased
        if(newCount>=1){
            this.setState({
                count: newCount  //set the decreased counter in state
            })
        }else{
            this.props.videoToggle(false); //send the state of video as false
            clearInterval(this.state.intervalID); //clears the counter interval
            this.props.gameAudio(this.props.themeSong); //send and play the themesong
            this.toQuizApp() //start the quiz (change page to QuizApp)
        }
    }

    componentDidMount(){
        
        if($(".toggle-volume i").html()==="volume_off") //if volume is off
        {
            $(".loading-container video").prop("muted",true); //mutes the loading video
        }
        this.props.videoToggle(true); //send the video state as true
        this.props.audioStop() //stop the theme song
        var intervalID = setInterval(this.handleCounter,1000); //interval for loading counter
        this.setState({intervalID: intervalID}); //save the counter interval id in state
    }

    componentWillUnmount(){
        clearInterval(this.state.intervalID); //clears interval when component unmounts
    }
    render(){
        let textStyle = "ready";
        if(this.state.count===2){
            textStyle = "set"
        }else if(this.state.count===1){
            textStyle = "go"
        }
        return (
            <StyleRoot>
                {
                    this.props.defaultLoading ?
                        <div className="loading-container-default" >
                            <div className="loading-display">
                                <div className="loading-name"><h1 style={nameAnimation}>{this.props.title.toUpperCase()}</h1></div>
                                <div className={"loading-counter " + textStyle}><h3 style={counterAnimation}>{this.state.count}</h3></div>
                            </div>
                        </div>
                    :
                        <div className="loading-container" style={{overflow: "hidden"}}>
                            <video src={loadingVisual} autoPlay={true} />
                        </div>
                }
            </StyleRoot>
        )
    }
}

function QuizStart(props){
    
    //go to loading page
    const toLoading=(username)=>{
        props.changePage(<Loading id={props.id} transitionSticker={props.transitionSticker} defaultLoading={props.defaultLoading}  gameAudio={props.gameAudio} themeSong={props.themeSong} audioStop={props.audioStop} videoToggle={props.videoToggle}   timerToggle={props.timerToggle} questions={props.questions} username={username} changePage={props.changePage} updatefromChild={props.updatefromChild} getCount={props.getCount}  getScore={props.getScore} title={props.title}  toHomePage={props.toHomePage} toLoading={toLoading}/>)
    }

    //entry form input validations
    const handleNameValidation = () => {
        let username = document.getElementById("name").value.trim(); //fetch username
        let patt=/^[A-Z]{1,10}[0-9]{0,3}?$/gi; //regex for validation
        
        //shows validation messages
        $(".invalid-feedback").text("Player name must contain 1-10 Alphabets & 0-3 Numbers!")

        if(!(patt.test(username))){ //if username does not fulfills regex conditions
            $("#name").removeClass("is-valid")
            $("#name").addClass("is-invalid")
        }else{
            $("#name").removeClass("is-invalid")
            $("#name").addClass("is-valid")
        }
    }

    //when enter is pressed
    const handleEnter = () => {
        let username = document.getElementById("name").value.trim(); //fetch username
        let patt=/^[A-Z]{1,10}[0-9]{0,3}?$/gi; //regex for validation
        
        if(!(patt.test(username))){ //if username does not fulfills regex conditions

            $("#name").removeClass("is-valid")
            $("#name").addClass("is-invalid")

            //prevent from entering the quizapp
            return;
        }
        if(localStorage.getItem(props.title)!=null) //if current quiz title is not null in local storage
        {
            let arr=JSON.parse(localStorage.getItem(props.title)).leaderBoard; //fetch leaderboard data corresponding to related quiz
            for(let i =0; i < arr.length; i++) //traversing the leaderboard array
            {
                if( username===arr[i].username) //if curr username matches the username that is already in leaderboard data
                {
                    //show confirm box
                    if(window.confirm("This Player Name Is Already Used! \nDo You Still Want To Play As "+username+" ?"))
                    {
                        break; //break out of loop if cancelled
                    }
                    else
                    {
                        $("#name").removeClass("is-valid")
                        $("#name").addClass("is-invalid")
                        $(".invalid-feedback").text("Enter Another Player Name!")
                        document.getElementById("name").value="";
                        return;
                    }
                }
            }
        }
        toLoading(username); //enter into loading page
    }

    //call handleEnter when 'Enter' key is pressed
    const pressEnter = (event) => {
        if(event.keyCode === 13){
            handleEnter();
        }
    }

    setTimeout(()=>{
        document.querySelector("body").style.overflowY = "hidden";
        $(".exit").hide()
        $(".menu-items").css({display: "inherit",background: "transparent", border: "none"})
        $(".toggle-volume").css(volumeToggleStyle)
        
        props.gameAudio(props.coverSong); //sets and play the cover song
    },10)

    
    
    
    return (
        <StyleRoot>
            <div className="entry-form" style={{overflow: 'hidden',backgroundImage: "URL("+props.poster+")"}}>
                <div className="entry-inputs" style={entryInputsAnimation}>
                    <div className="form-group d-flex flex-column justify-content-center align-items-center" style={{marginBottom: "0"}}>
                        <input onChange={handleNameValidation} onKeyDown={pressEnter} className="form-control" id="name" type="text" placeholder="NAME"/>
                        <div className="valid-feedback" style={{textAlign: "center"}}>
                            This Player Name looks good!
                        </div>
                        <div className="invalid-feedback" style={{padding: "0 30px", textAlign: "center"}}>
                            Player name must contain 1-10 Alphabets &amp; 0-3 Numbers!
                        </div>
                    </div>
                    <button onClick={handleEnter}>ENTER
                        <i className="material-icons" style={{position:"absolute",left: "20px", top: "13px"}}>play_arrow</i>
                    </button>
                    <button 
                        onClick={() => {props.toHomePage(); props.audioStop()}}
                    >
                        <i className="material-icons" style={{position:"absolute",left: "20px", top: "13px"}}>home</i>
                        GO BACK
                    </button>
                </div>
            </div>
        </StyleRoot>
    )

}



export default QuizStart;