import React from 'react';
import "../../Styles/Leaderboard.css";
import quizCardsData from '../../QuizData/quizCardsData';
import DisplayLeaderboard from './LeaderboardComponents/DisplayLeaderboard';

class Leaderboard extends React.Component
{
    constructor()
    {
        super();
        this.state={
            leaderboard : [], //stores the leaderboard data
            showhead: false //check if data is found or not
        }

    }

    //handles the combobox selection
    handleSelect = (event) => {
        const {value} = event.target;
        if(JSON.parse(window.localStorage.getItem(value))!=null){ //if leaderboard data is found is found for particular quiz
            //fetches the leaderboard from localStorage and sets into the state
            this.setState({
                leaderboard : JSON.parse(window.localStorage.getItem(value)).leaderBoard,
                showhead: false
            })
        }else{
            //if data is not found
            this.setState({showhead: true})
        }
    }

    componentDidMount()
    {

        if(window.localStorage.length!==0){ //if local storage is not empty

            //fetch the leaderboard data corresponding to related quiz and parse to json format
            if(JSON.parse(window.localStorage.getItem(quizCardsData[0].title))!==null){
                //sets the leaderboard corresponding to related quiz
                let temp = JSON.parse(window.localStorage.getItem(quizCardsData[0].title)).leaderBoard
                this.setState({leaderboard : temp})
            }else{ //if leaderboard data not found
                //show not found message
                this.setState({showhead: true})
            }
        }
        else{
            //show not found message
            this.setState({showhead: true})
        }
    }
  
     render(){
        //data not found message
        let heading = (<h4 style={{marginTop: "30px",textAlign: "center"}}>No Data Found!</h4>);

        //set combo box options
        const options = quizCardsData.map(data => {
            return (
                <option key={data.id} value={data.title}>{data.title}</option>
            )
        })
        return (
            <div >
                <div style={{position: "sticky",top: "160px",zIndex: 1}}>
                    <select name="selected" onChange={this.handleSelect} className="custom-select custom-select-lg leaderboard-combobox">
                        {options}
                    </select>
                </div>
                <div >
                    {this.state.showhead ? heading : <DisplayLeaderboard leaderboard={this.state.leaderboard}/>}
                </div>
            </div>

        )
     }
}

export default Leaderboard;