
import React from 'react';
import ScoreBar from './ScoreBar';

class DisplayLeaderboard extends React.Component{
    constructor(props){
        super();
        this.state = {
            leaderBoard: props.leaderboard, //leaderboard data
        }
    }

    //sets the leaderboard data everytime it recieve new props
    static getDerivedStateFromProps(nextProps)
    {
	   return{
		   leaderBoard:nextProps.leaderboard
	   }
    }
	

    //shuffle the array
	shuffleArray = (arr) => {
		var i = arr.length, k , temp;    
        // k is to generate random index and temp is to swap the values
        while(--i > 0){
            k = Math.floor(Math.random() * (i+1));
            temp = arr[k];
            arr[k] = arr[i];
            arr[i] = temp;
        }

	}

    //static variable for maintaining the scorebar key
    static counterProperty=0;

    //handles counterProperty
    static counter=()=>{
        if(DisplayLeaderboard.counterProperty>10)
        {
            DisplayLeaderboard.counterProperty=0;
        }
        return DisplayLeaderboard.counterProperty++;
    }

   

    render(){
        //array of scorebar colors
        let ColorList=["#00FFFF","#008080","#FF7F50","#5F9EA0","#32CD32","#FFA500","#6A5ACD","#ee82ee","#DEB887","#8B4513"];
        
        //shuffle ColorList Array
		this.shuffleArray(ColorList)
    
        let i=1; //scorebar rank
        let k=0,j=0; //traversing colorlist array
        
        //displays the top ten contenders (score - rankbar - name)
        let leaderBoard = this.state.leaderBoard.map(contender =>{
            return(
                <tr key={contender.username} >
                    <td  style={{textAlign:"center"}} className="hide-data">{contender.score}</td>
                    <td style={{width:"70vw"}}>
                        <ScoreBar scoreBarRank={i++} username={contender.username} score={contender.score} key={DisplayLeaderboard.counter()} scoreBarColor={ColorList[k++]} scoreBarRankColor={ColorList[j++]}/>
                    </td>
                    <td  style={{textAlign:"center"}} className="hide-data">{contender.username}</td>
                </tr>
            )
        })

        return(
            <table cellPadding="15" cellSpacing="15">
                <tbody>
                    <tr>
                        <th className="hide-data">
                            Score
                        </th>
                        <th style={{textAlign:"center"}}>
                            Rank Bar
                        </th>
                        <th className="hide-data">
                            Contender
                        </th>
                    </tr>
                    {leaderBoard}
            </tbody>
            </table>
        );
     }
}

export default DisplayLeaderboard;