import React from "react"
import '../../../Styles/ScoreBar.css'
import $ from 'jquery';

class ScoreBar extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state={
            intervalID:null, //interval id of scorebar animation
            scorePercentage:0 //scorebar percentage
        }
    }

    //handling scorebar percentage
    handlescorePercentage=()=>{
        var newScorePercentage=this.state.scorePercentage+1;
        if( newScorePercentage<=100-(2*(this.props.scoreBarRank-1)-1))
        {
            this.setState({
            scorePercentage:newScorePercentage
            })
        }

        else{
            $(".cont-score").show() //shows contender score inside scorebar when it completes (Mobile view)
            clearInterval(this.state.intervalID);
        }
    }

    componentDidMount(){
        $(".cont-score").hide() //hides contender score inside scorebar (desktop)

        //calls the handlescorePercentage method after every 10ms until its interval completes
        var intervalID = setInterval(this.handlescorePercentage,10);
        this.setState({intervalID: intervalID})
    }

    componentWillUnmount(){
        //clear scorePercentage interval when it unmounts
        clearInterval(this.state.intervalID);
    }

    render()
    {
        return(
            <div className="scoreBarOuterSection">
                <div className="scoreBarInnerSection" style={{width:this.state.scorePercentage+"%" , backgroundColor:this.props.scoreBarColor}}>   
                    <div className="show-data contender-details"><div>{this.props.username}</div><div className="cont-score">{this.props.score}</div></div>
                    
                    <div className="scoreBarRank" style={{right:"-9px", top:"-8.5px",backgroundColor:this.props.scoreBarRankColor}} >
                        {this.props.scoreBarRank}
                    </div>
                </div>                        
            </div>
        )
    }
}
export default ScoreBar