import React from 'react';
import '../Styles/QuizEnd.css';
import $ from 'jquery'
import Radium,{StyleRoot} from 'radium';
import {fadeIn} from 'react-animations';

import partypoppersAnim from '../static/shared-resources/partypoppers.gif'
import partypoppersMobile from '../static/shared-resources/partypoppersmobile.gif'
import partypopperSound from '../static/shared-resources/partypoppersound.mp3'


//score animation (style object)
const scoreAnimation = {
    animation: "1s 1 ease-in-out",
    animationName: Radium.keyframes(fadeIn, "scoreAnimation")
}

//buttons animation (style object)
const buttonsAnimation = {
    animation: "1s 1 ease-in-out",
    animationName: Radium.keyframes(fadeIn, "buttonsAnimation")
}

class QuizEnd extends React.Component{
    constructor(props){
        super();
        this.state = {
            score:props.score, //final score
            username:props.username, //username/playername
            title:props.title, //title of current quiz
            highScore:0, //highscore
            highScorer:"", //name of the highscorer
            isHighScore:false, //if highscore is achieved by the current user
            popperSound:null, //popper sound
        }
    }

    componentDidMount(){
        $(".menu-btn").css({color: "white"}); //change menu btn color to white


        if(window.localStorage.length===0 /* if localstorage is empty */ || JSON.parse(window.localStorage.getItem([this.state.title])===null) /* if current quiz leaderboard data is null */){ 
            window.localStorage.setItem([this.state.title],JSON.stringify({leaderBoard:[{score:this.state.score , username:this.state.username}]})) //set the leaderboard data corresponding the current quiz
            //set updated highscore and highscorer
            this.setState(prevState=>{
                return {
                        highScore:prevState.score,
                        highScorer:prevState.username,
                        isHighScore: true
                    }
            })
         }
         else{
             //fetch highscore and highscorer of current quiz from localStorage
           let highScore=JSON.parse(window.localStorage.getItem([this.state.title])).leaderBoard[0].score;
           let highScorer=JSON.parse(window.localStorage.getItem([this.state.title])).leaderBoard[0].username;
        

           if(this.state.score>highScore){ //if curr score is greater than prev highscore
                //set updated highscore and highscorer
                this.setState(prevState=>{
                    return {
                            highScore:prevState.score,
                            highScorer:prevState.username
                        }
                    }
                )
                this.setState({isHighScore:true})
           }
           else{    //if curr score is <= prev highscore
                //set fetched highscore and highscorer
                this.setState({highScore:highScore , highScorer:highScorer})
           }

           //fetch leaderboard data from localStorage and parse into json format
           let newLeaderBoard=JSON.parse(window.localStorage.getItem([this.state.title])).leaderBoard.slice();
           
           //traversing fetched leaderboard data
           let i=0;
           for(; i<newLeaderBoard.length;i++){
               if(newLeaderBoard[i].username===this.state.username){ //if curr username matches the username that is already in leaderboard
                   if(newLeaderBoard[i].score<this.state.score) { //if score corresponding to matched username < curr username's score
                        newLeaderBoard.splice(i,1); //delete the entry of that particular matched username from fetched leaderboard
                        i=newLeaderBoard.length;
                        break;
                   }
                   else { //if score corresponding to matched username >= curr username's score
                        //do nothing
                        break;
                   }
               }
           }

           if(i===newLeaderBoard.length) {
                i=0;
                for(; i<newLeaderBoard.length;i++) {
                    if(newLeaderBoard[i].score<this.state.score) {
                        //adds the curr user record in its sorted position
                        newLeaderBoard.splice(i,0,{score:this.state.score , username:this.state.username})
                        break;
                    }
                }
                if(i===newLeaderBoard.length && newLeaderBoard.length<10){ //if curr user score is less than all prev players' scores and also less than 10
                    //add the curr user record to the last position in the leaderboard
                    newLeaderBoard.push({score:this.state.score , username:this.state.username})
                }

            }
            if(newLeaderBoard.length>10)
            {
                newLeaderBoard.splice(10,1); //delete 11th record from fetched leaderboard
            }
            //set the updated leaderboard to localStorage
            window.localStorage.setItem([this.state.title],JSON.stringify({leaderBoard:newLeaderBoard}));
        
        }
        
        //logic and css for highscore poppers' animation
        $(".party-poppers").hide();
        $(".party-poppers-mobile").hide();
        let popperSound = new Audio(partypopperSound); 
        popperSound.volume = 0.35;
        this.setState({popperSound:popperSound}); //set the popper audio to the state
        
    }

    componentDidUpdate()
    {
        setTimeout(() => {
            
            if(this.state.isHighScore){ //if curr user achieved highscore
                if($(".quiz-end-container").width()>=768){ //if screen width is >=768px 
                    $(".party-poppers").fadeIn("slow") //show popper's animation effect (desktop)
                }else{
                    $(".party-poppers-mobile").fadeIn("slow") //show popper's animation effect (mobile)
                }

                if($(".toggle-volume i").html() !== "volume_off"){ //if volume is on
                   this.state.popperSound.play(); //play popper sound
                }         
                
            }
        }, 1000);
        
    }

    componentWillUnmount()
    {
        //pause the popper sound when it unmounts
        if(!this.state.popperSound.paused)
        this.state.popperSound.pause();
    }

    //resets the que count and curr score
    resetGame=()=>{
        this.props.updatefromChild(0,0);
    }

    //go back to homepage
    backToHome=()=>{
        this.resetGame();
        this.props.audioStop();
        this.props.toHomePage();
    }

    //play the quiz again
    playAgain=()=>{
        this.resetGame();
        this.props.audioStop();
        this.props.toLoading(this.state.username);
    }
    
    render(){
        setTimeout(() => {
            $(".party-poppers").fadeOut("slow")
            $(".party-poppers-mobile").fadeOut("slow")
        }, 2000);
        
        return(
            <StyleRoot>
                <div className="quiz-end-container">
                    <div className="party-poppers-mobile" style={{backgroundRepeat: "repeat-y",backgroundPosition:"center",backgroundImage:"Url("+partypoppersMobile+")"}}>
                    </div>
                    <div className="party-poppers" style={{ left:"-83px",backgroundImage:"Url("+partypoppersAnim+")"}} />
                    <div className="party-poppers" style={{ transform:"rotateY(180deg)",right:"-83px", backgroundPosition:"center",backgroundImage:"Url("+partypoppersAnim+")"}} />
                    <div className="final-score-container" style={scoreAnimation}>
                        <div className="final-score-title">Score</div>
                        <div className="final-score">{this.state.score}</div>
                        <div className="current-username">{this.state.username}</div>
                    </div>

                    <div className="highscore-container" style={scoreAnimation}>
                        <div className={"highscore-title " + (this.state.isHighScore ? "new-highscore-title":"")}>
                            {this.state.isHighScore ? "New High Score!":"High Score"}
                        </div>
                        <div className="highscore">
                            {this.state.highScore}
                        </div>
                        <div className="highscorer">
                            {this.state.highScorer}
                        </div>
                    </div>

                    <div className="end-btns-container" style={buttonsAnimation}>
                        <button 
                            onClick={this.backToHome}
                            className="home-btn"
                            title="Back To Home"
                        >
                            <i className="material-icons" style={{fontSize: "40px"}}>home</i>
                        </button>
                        <button 
                            onClick={this.playAgain}
                            className="playagain-btn"
                            title="Play Again"
                        >
                            <i className="material-icons" style={{fontSize: "40px"}}>refresh</i>
                        </button>
                    </div>
                </div>
            </StyleRoot>
        )
    }
}
export default QuizEnd